import React, { useEffect, useRef, useState } from 'react';
import bridge from '@vkontakte/vk-bridge';
import './App.css';
import {BrowserRouter, HashRouter,Route,Routes} from 'react-router-dom'
import Home from './Components/Home';
import ThisDay from './Components/ThisDay';
import ThisTime from './Components/ThisTime';
import ThisMath from './Components/ThisMath'
import ThisYear from './Components/ThisYear'
import {
  AdaptivityProvider,
  ConfigProvider,
  AppRoot,
  SplitLayout,
  SplitCol,
  View,
  Panel,
  PanelHeader,
  Header,
  Group,
  SimpleCell,
} from '@vkontakte/vkui';
import '@vkontakte/vkui/dist/vkui.css';



function App() {
  const [data,setData] = useState<string[]>([])
  const [dataEng,setDataEng] = useState<string[]>([])
  const [click,setClick] = useState<boolean>(false)
  const [rec,setRec] = useState<boolean>(false)

  const inputRef   = useRef<HTMLButtonElement | null>(null);

  (bridge as any).send('VKWebAppCheckNativeAds', { ad_format: 'interstitial' })
  .then((data:any) => {
    if (data.result) {
   
    } else {
      console.log('Рекламные материалы не найдены.');
    }
  })
  .catch((error:any) => { console.log(error); /* Ошибка */  });

  function fooButtonClick(){
    // Показать рекламу
    (bridge as any).send('VKWebAppShowNativeAds', { ad_format :  'interstitial' })
      .then((data:any) => {
        if (data.result) // Успех
          console.log('Реклама показана');
        else // Ошибка 
          console.log('Ошибка при показе');
      })
      .catch((error:any) => { console.log(error); /* Ошибка */ });
  }


  async function send() {
    if (rec === true){
      fooButtonClick()
    }
    setRec(true)

    if (click === true){
      return
    }
    setClick(true)
    
  
    var d = new Date();
    var dayZ = d.getDate();
    var mouthZ = d.getMonth() + 1
    console.log(dayZ)
    console.log(mouthZ)
    try {
      let response = await fetch(`https://cors-anywhere.herokuapp.com/http://numbersapi.com/${mouthZ}/${dayZ}/date?json`)
      let result = await response.json()
      console.log('hhe',result)
      setDataEng([...dataEng,result.text])
      if(dataEng.includes(result.text)  ){
        console.log('такой уже естьтакой уже естьтакой уже есть')
        send()
        return;
      }else{
      
      let resTrans = translate(result.text)
      console.log('ruus',resTrans)
      console.log('rr',result)
      }
      
    } catch (error) {
      
    }finally{
      const kek = ()=>{
        setClick(false)
      }
      setTimeout(kek,200)

    }
    
  }

  async function send2() {

    var d = new Date();
    var dayZ = d.getDate();
    var mouthZ = d.getMonth() + 1
    console.log(dayZ)
    console.log(mouthZ)

    let response = await fetch(`https://cors-anywhere.herokuapp.com/http://numbersapi.com/${mouthZ}/${dayZ}/date?json`)
    let result = await response.json()
    console.log('hhe',result)
    setDataEng([...dataEng,result.text])
    if(dataEng.includes(result.text)  ){
      console.log('такой уже естьтакой уже естьтакой уже есть')
      send()
      return;
    }else{
    
    let resTrans = translate(result.text)
    console.log('ruus',resTrans)
    console.log('rr',result)
    }
    
    
  }

  useEffect(()=>{
      send()  
      const kek1 = ()=>{
        inputRef.current?.click() 
      }
      setTimeout(kek1,1000)
      
      const kek = ()=>{
        inputRef.current?.click() 
      }
      setTimeout(kek,1500)
  },[])
  
  
  

  


  function translate(result:string){
    var sourceText = result
    var sourceLang = 'en';
    var targetLang = 'ru';
    console.log(sourceText);
    
    var url = "https://translate.googleapis.com/translate_a/single?client=gtx&sl="+ sourceLang + "&tl=" + targetLang + "&dt=t&q=" + encodeURI(sourceText);
    console.log(url);
    
    async function mde() {
      let response = await fetch(url);
      let result = await response.json();
      const resultData : string = result[0][0][0]
      console.log('vaa',resultData);
      if(data.includes(resultData) ){
        send()
      }else{
        setData([...data,resultData])
      }
     
    }
    mde()
  }
 
  
  

  return (
    <div className="App">
      <HashRouter>
        <Routes>
          <Route path='/' element={<Home />} />
          <Route path='/Thisday' element={<ThisDay />} />
          <Route path='/Thistime' element={<ThisTime />} />
          <Route path='/Thismath' element={<ThisMath />} />
          <Route path='/Thisyear' element={<ThisYear />} />
        </Routes>
			</HashRouter>	
    </div>
  );
}

export default App;
