import React, { useEffect, useRef, useState } from 'react';
import bridge from '@vkontakte/vk-bridge';
import { Link, useParams } from 'react-router-dom'
import { Button,Snackbar ,Group,Div } from '@vkontakte/vkui';
import {Icon28ErrorCircleOutline,Icon20HelpOutline,Icon20ArrowUturnLeftOutline, Icon20FavoriteCircleFillYellow,Icon20NotificationOutline ,Icon20CrownCircleFillVkDating} from '@vkontakte/icons';
import './../App.css';
import axios from 'axios';
import Skeleton from '@mui/material/Skeleton';
import Stack from '@mui/material/Stack';
import { useReclama } from '../Store';



// const Button = (props:ButtonProps)=>{
//   return <div>{props.children}</div>
// }



function ThisDay() {
  const [data,setData] = useState<string[]>([])
  const [dataEng,setDataEng] = useState<string[]>([])
  const [click,setClick] = useState<boolean>(false)
  const [rec,setRec] = useState<boolean>(false)
  const [error,setError] = useState<boolean>(false)
  const [conditionValue,setContditionValue] = useState<boolean>(false)
  const [text, setText] = useState<string>('');
  const [snackbar, setSnackbar] = useState<any>(null);
  const [burger,setBurger] = useState<boolean>(true)

  const inputRef   = useRef<HTMLButtonElement | null>(null);

  (bridge as any).send('VKWebAppCheckNativeAds', { ad_format: 'interstitial' })
  .then((data:any) => {
    if (data.result) {
   
    } else {
      console.log('Рекламные материалы не найдены.');
    }
  })
  .catch((error:any) => { console.log(error); /* Ошибка */  });

  function fooButtonClick(){
    // Показать рекламу
    (bridge as any).send('VKWebAppShowNativeAds', { ad_format :  'interstitial' })
      .then((data:any) => {
        if (data.result) {// Успех
          console.log('Реклама показана');
          setBurger(true)}
        else {// Ошибка 
          console.log('Ошибка при показе');
          setBurger(true)}
        }
      )
      .catch((error:any) => { console.log(error); /* Ошибка */ });
  }
  function fooButtonClickReward(){
    // Показать рекламу
    (bridge as any).send('VKWebAppShowNativeAds', { ad_format :  'reward' })
      .then((data:any) => {
        if (data.result) // Успех
          console.log('Реклама показана');
        else // Ошибка 
          console.log('Ошибка при показе');
      })
      .catch((error:any) => { console.log(error); /* Ошибка */ });
  }

  const switchReclama = useReclama((state:any)=>state.setPokazReclami)
	const itemReclama = useReclama((state:any)=>state.pokazReclami )

  useEffect(()=>{
    if (itemReclama === false){
      setBurger(false)
    }
    const showRec = ()=>{

        if (itemReclama === false){
          
          fooButtonClick()
          switchReclama()
        }
    }
    setTimeout(showRec,2000)
  },[])
 


  async function send() {
    if (conditionValue===true){
      openError()
      return;
    }
    // if (rec === true){
    //   fooButtonClickReward()
    // }
    // setRec(true)

    if (click === true){
      return
    }
    setClick(true)
    
  
    var d = new Date();
    var dayZ = d.getDate();
    var mouthZ = d.getMonth() + 1
    console.log(dayZ)
    console.log(mouthZ)
    try {
      let result1:any = await axios.get(`https://cors-anywhere.herokuapp.com/http://numbersapi.com/${mouthZ}/${dayZ}/date?json`)
      let result = result1.data

      console.log('hhe',result)
      setDataEng([...dataEng,result.text])
      if(dataEng.includes(result.text)  ){
        console.log('такой уже естьтакой уже естьтакой уже есть')
        send()
        return;
      }else{
      let res = result.text
      let Zresult = res.replace(/is/gi,'-')
      console.log('ssssssssssss',Zresult)
      let resTrans = translate(Zresult)
      console.log('ruus',resTrans)
      console.log('rr',result)
      }
      
    } catch (error) {
      
    }finally{
      const kek = ()=>{
        setClick(false)
        if (rec === true){
          fooButtonClickReward()
        }
        setRec(true)
        
      }
      setTimeout(kek,200)

    }
    
  }

  async function send2() {

    var d = new Date();
    var dayZ = d.getDate();
    var mouthZ = d.getMonth() + 1
    console.log(dayZ)
    console.log(mouthZ)

    let response = await fetch(`https://cors-anywhere.herokuapp.com/http://numbersapi.com/${mouthZ}/${dayZ}/date?json`)
    let result = await response.json()
    console.log('hhe',result)
    setDataEng([...dataEng,result.text])
    if(dataEng.includes(result.text)  ){
      console.log('такой уже естьтакой уже естьтакой уже есть')
      send()
      return;
    }else{
    
    let resTrans = translate(result.text)
    console.log('ruus',resTrans)
    console.log('rr',result)
    }
    
    
  }
  let kek1
  let kek

  useEffect(()=>{
      send()  
      kek1 = ()=>{
        inputRef.current?.click() 
      }
      setTimeout(kek1,1500)
      
      kek = ()=>{
        inputRef.current?.click() 
        if (data.length < 3){
          console.log('lee',data.length)
          inputRef.current?.click() 
          // inputRef.current?.click() 
          // setTimeout(time,5000)
          // setError(true)
        }
      }
      setTimeout(kek,2500)
  },[])
  
  
  
  window.addEventListener('online',  updateOnlineStatus);
  window.addEventListener('offline', updateOnlineStatus);
  let condition
  function updateOnlineStatus(event:any) {
   condition = navigator.onLine ? "online" : "offline";
  // document.body.className = condition;
  console.log(condition);
  if (condition === 'offline'){
    setContditionValue(true)
  }
  if (condition === 'online'){
    setContditionValue(false)
  }
  
}
  


  function translate(result:string){
    var sourceText = result
    var sourceLang = 'en';
    var targetLang = 'ru';
    console.log(sourceText);
    
    var url = "https://translate.googleapis.com/translate_a/single?client=gtx&sl="+ sourceLang + "&tl=" + targetLang + "&dt=t&q=" + encodeURI(sourceText);
    console.log(url);
    
    async function mde() {
      let response = await fetch(url);
      let result = await response.json();
      const resultData : string = result[0][0][0]
      console.log('vaa',resultData);
      if(data.includes(resultData) ){
        send()
      }else{
        let resss = resultData.replace(/—/gi,'-')
        setData([...data,resss])
      }
     
    }
    mde()
  }
  // const tt = ()=>{
  //     useEffect(()=>{
  //       const time = ()=>{
  //         if (data.length < 3){
  //           setError(true)
  //         }
  //       }
  //       time()
        
  //     },[])
  // }
  // setTimeout(tt,5000)
 
    // const time = ()=>{
    //   if (data.length < 3){
    //     console.log('lee',data.length)
    //     // inputRef.current?.click() 
    //     // setTimeout(time,5000)
    //     // setError(true)
    //   }
    // }
    // setTimeout(time,4000)

 
 

  const openError = () => {
    if (snackbar) return;
    setSnackbar(
      <Snackbar
        onClose={() => setSnackbar(null)}
        before={<Icon28ErrorCircleOutline fill="var(--vkui--color_icon_negative)" />}
      >
        Отсуствует доступ к интернету.
      </Snackbar>,
    );
  };
  

  return (
    <>
    {burger===false? <div className='textStyle'>Идет загрузка...</div> : <>
      <div className='btnNazad'>
      <Link  to='/' >
          <Button align='center'  size='l'>
              <div className='btnKek'>
                  <Icon20ArrowUturnLeftOutline/><p>Назад</p>
              </div>
          </Button>
      </Link>
    </div>
    <div className=''>

    <div className="App">
      {data.length > 2? <div>{[...new Set(data)].map((item)=>{
        return(
          <div className='itemStyle itemStyle2' key={item}>{item}</div>
        )
      })}
      </div>
      :  <>
      <Skeleton  className='itemStyle itemStyle1' sx={{ bgcolor: 'azure' }}  variant="text"  />
      <Skeleton  className='itemStyle itemStyle1' sx={{ bgcolor: 'azure' }}  variant="text"  />
      <Skeleton  className='itemStyle itemStyle1' sx={{ bgcolor: 'azure' }}  variant="text"  />
      </>
      }
      
     

      {data.length < 11&&
        <Button className='tik' onClick={send}><p className='tikchild'>Получить новые факты (после просмотра рекламы)</p></Button>
      }
      <button ref={inputRef} className='tik zero' onClick={send2}>тык</button>
    </div>
    {error&& <p>Произошла ошибка. Попробуйте повторить позже</p>}
    {text && (
          <Group>
            <Div>{text}</Div>
          </Group>
        )}

        {snackbar}
      </div>
      </>}
    </>
  );
}

export default ThisDay;
