import React from 'react'
import { Link } from 'react-router-dom'
import {  Button } from '@vkontakte/vkui';
import {useState} from 'react'

const Home = () => {
  const [conditionValue,setContditionValue] = useState<boolean>(false)

  window.addEventListener('online',  updateOnlineStatus);
	  window.addEventListener('offline', updateOnlineStatus);
	  let condition
		function updateOnlineStatus(event:any) {
		 condition = navigator.onLine ? "online" : "offline";
		// document.body.className = condition;
		console.log(condition);
		if (condition === 'offline'){
			setContditionValue(true)
		}
		if (condition === 'online'){
			setContditionValue(false)
		}
		
	}

  return (
    <>
    {conditionValue === false&&<div className='conteiner'>
        <div className="col-md-12 text-center">
            <h3 className="animate-charcter"> Узнайте, что скрывает цифра дня!</h3>
        </div>

        <Link className='parentBtn parentBtnTop' to='Thisday'>
            <Button  className='btnStyle'  >
                <div className='btnStyleChild'>
                    <div className=''>
                        <img className='imgStyle' src='https://svgshare.com/i/uv4.svg' alt='no'/>
                    </div> 
                        <p className='pStyle'>Этот день<br></br> в истории</p>
                </div>
            </Button>
        </Link>
        <Link className='parentBtn' to='Thistime'>
            <Button  className='btnStyle'  >
                <div className='btnStyleChild'>
                    <div className=''>
                        <img className='imgStyle' src='https://svgshare.com/i/uuj.svg' alt='no'/>
                    </div> 
                    <p className='pStyle'>Цифра дня<br></br> и ее факты</p>
                </div>
            </Button>
        </Link>
        <Link className='parentBtn' to='Thismath'>
            <Button  className='btnStyle'  >
                <div className='btnStyleChild'>
                    <div className=''>
                        <img className='imgStyle' src='https://svgshare.com/i/uuk.svg' alt='no'/>
                    </div> 
                        <p className='pStyle'>Цифра дня<br></br>в математике</p>
                </div>
            </Button>
        </Link>
        <Link className='parentBtn' to='Thisyear'>
            <Button  className='btnStyle'  >
                <div className='btnStyleChild'>
                    <div className=''>
                        <img className='imgStyle' src='https://svgshare.com/i/uv7.svg' alt='no'/>
                    </div> 
                        <p className='pStyle'>Случайный <br></br>факт года</p>
                </div>
            </Button>
        </Link>
    </div>}
    {conditionValue && 
        <div className='redParent'>
            <p className ='red'>Потеряна связь с интернетом</p>
        </div>}
    </>
  )
}

export default Home